import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { SET_BASEDATA } from 'store/actions';

// material-ui

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Languages } from './Languages';
import UserInfo from './component/UserInfo';
import UserLogin from './component/UserLogin';

// ==============================|| SINGLE PAGE APPLICATION ||============================== //

const Home = () => {
    console.log('home app running');
    const dispatch = useDispatch();
    const globalization = useSelector((state) => state.globalization);
    const browserLocale = /zh/i.test(navigator.language) ? 'zhCN' : 'enUS';
    const [baseData, setBaseData] = useState(globalization.baseData); //单向数据流
    const localization = useSelector((state) => state.localization); //read only (language related)
    let [langPack, setLangPack] = useState(Languages[localization.locale]); //currentLanguage
    const [loginData, setLoginData] = useState(baseData.loginData); //for Home

    useEffect(() => {
        dispatch({ type: SET_BASEDATA, baseData });
        window.loginData = baseData.loginData;
    }, [dispatch, baseData]); //全局数据修改，因为登录

    useEffect(() => {
        setLoginData(globalization.baseData.loginData);
        return () => {};
    }, [globalization.baseData]); //订阅全局数据变化

    useEffect(() => {
        setLangPack(Languages[localization.locale || browserLocale]);
        return () => {};
    }, [localization.locale]); //订阅语言变化

    useEffect(() => {
        if (window.baseData) {
            setBaseData(window.baseData);
        } //适配插件模式
        return () => {};
    }, []); //启动项

    useEffect(() => {
        console.log('I am at home');
        return () => {
            console.log('I leave home');
        };
    }, []);

    //____________一些函数_______________________________________________

    async function doLogin(obj) {
        //用于获取API
        if (!obj.userEmail || !obj.userName) return;
        let queryObj = prepareMsg('ERP用户信息');
        queryObj.request.data = obj;
        let msg = await doFetch(queryObj);
        console.log(msg);
        let { status, result } = msg.response.data;
        if (!status) return queryResult(false, result);
        setBaseData(Object.assign({}, baseData, { loginData: result })); //全局调用
        //setTimeout(getServiceInfo, 1234);
        return msg;
    }

    /* async function getServiceInfo() {
        return;
    } */

    //____________JSX 函数_______________________________________________

    return (
        <MainCard title="Home --主页">
            {loginData ? <UserInfo langPack={langPack} loginData={loginData} /> : <UserLogin doLogin={doLogin} langPack={langPack} />}
        </MainCard>
    );
};

export default Home;
