// material-ui
import { Typography, Link } from '@mui/material';
import PropTypes from 'prop-types';

function UserInfo({ langPack, loginData }) {
    if (!loginData) return null;
    return (
        <>
            <Typography variant="h2">{langPack.account.accountInfo}</Typography>
            <div style={{ paddingTop: '20px' }}></div>
            <Typography>
                {langPack.account.userName}: {loginData.userName}
            </Typography>
            <Typography>
                {langPack.account.email}: {loginData.userEmail}
            </Typography>
            <Typography>
                {langPack.account.expiry}: {loginData.r1_valid ? new Date(Number(loginData.r1_valid)).toLocaleString() : ''}
            </Typography>
            <div style={{ paddingTop: '50px' }}></div>
            <Typography variant="h2">{langPack.account.usefulLink}</Typography>
            <div style={{ paddingTop: '20px' }}></div>
            <Typography>
                {langPack.linkText.MA}{' '}
                <Link href={langPack.link.MA} target="_blank">
                    {langPack.link.MA}
                </Link>
            </Typography>
            <Typography>
                {langPack.linkText.AliSupplier}{' '}
                <Link href={langPack.link.AliSupplier} target="_blank">
                    {langPack.link.AliSupplier}
                </Link>
            </Typography>
            <Typography>
                {langPack.linkText.LearningCenter}{' '}
                <Link href={langPack.link.LearningCenter} target="_blank">
                    {langPack.link.LearningCenter}
                </Link>
            </Typography>
            <div style={{ paddingTop: '50px' }}></div>
            <Typography variant="body1">{langPack.about.copyright}</Typography>
        </>
    );
}

UserInfo.propTypes = {
    langPack: PropTypes.object,
    loginData: PropTypes.object
};

export default UserInfo;
