import { useState } from 'react';
import PropTypes from 'prop-types';

// material-ui
import { Typography, Button, ButtonGroup, TextField } from '@mui/material';
import { Check as CheckIcon, Cancel as CancelIcon } from '@mui/icons-material';

function UserLogin({ doLogin, langPack }) {
    let [userEmail, setUserEmail] = useState('');
    let [userName, setUserName] = useState('');
    let [codeValue, setCodeValue] = useState('');
    return (
        <>
            <TextField
                fullWidth
                margin="normal"
                id="userName"
                variant="outlined"
                autoComplete="on"
                onChange={(e) => setUserName(e.target.value)}
                value={userName}
                label={<Typography variant="h3">{langPack.account.userName}</Typography>}
            />
            <TextField
                fullWidth
                margin="normal"
                id="email"
                variant="outlined"
                autoComplete="on"
                value={userEmail}
                onChange={(e) => setUserEmail(e.target.value)}
                label={<Typography variant="h3">{langPack.account.email}</Typography>}
            />
            <TextField
                margin="normal"
                id="codeValue"
                variant="outlined"
                autoComplete="on"
                value={codeValue}
                onChange={(e) => setCodeValue(e.target.value)}
                onKeyUp={(e) => {
                    if (e.key == 'Enter') {
                        e.preventDefault();
                        e.stopPropagation();
                        doLogin({ userEmail, userName, codeValue });
                    }
                }}
                label={<Typography variant="h3">{langPack.account.verificationCode}</Typography>}
            />
            <Button
                variant="contained"
                sx={{ marginTop: '20px', marginLeft: '10px' }}
                onClick={() => {
                    if (!userEmail || !userName) return queryResult(false, langPack.account.formIncomplete);
                    doLogin({ userEmail, userName, codeType: 'getCode' });
                }}
            >
                {langPack.account.applyCode}
            </Button>
            <hr />
            <ButtonGroup sx={{ marginTop: '20px' }}>
                <Button startIcon={<CheckIcon />} onClick={() => doLogin({ userEmail, userName, codeValue })}>
                    {langPack.common.yes}
                </Button>
                <Button startIcon={<CancelIcon />} onClick={() => location.reload()}>
                    {langPack.common.no}
                </Button>
            </ButtonGroup>
        </>
    );
}

UserLogin.propTypes = {
    langPack: PropTypes.object,
    doLogin: PropTypes.func
};

export default UserLogin;
